@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
body {
  padding: 0;
  font-family: "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1000px;
  margin: 0 auto;
}

.container {
  margin-top: 90px;
}

body {
  font-size: 18px;
}

button {
  padding: 0.5rem 1rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1rem;
  background-color: rgb(50, 35, 88);
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h1,
h2,
h3,
h4 {
  color: rgb(50, 35, 88);
}

h1 {
  display: grid;
  place-content: center;
}

h3,
h4 {
  margin-bottom: 0;
}

nav {
  left: 0;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  /* max-width: 1000px; */
  background: white;
  box-shadow: 0px 2px 15px -8px rgba(181, 170, 255, 1);
  -webkit-box-shadow: 0px 2px 15px -8px rgba(181, 170, 255, 1);
  -moz-box-shadow: 0px 2px 15px -8px rgba(181, 170, 255, 1);
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: rgb(50, 35, 88);
  font-weight: bold;
  padding: 16px 32px;
  border-radius: 3px;
}

nav a:hover {
  background-color: rgb(50, 35, 88);
  color: white;
}

.selected_route{
  background-color: rgb(50, 35, 88);
  color: white;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 10px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid rgb(135, 110, 204);
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input,
textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button,
p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.main-header {
  font-size: 10vw;
  font-style: italic;
}
.sub-header {
  font-size: 4vw;
}

@media screen and (max-width: 1000px) {
  body {
    margin: 0 30px;
  }
}
@media screen and (max-width: 600px) {
  body {
    margin: 0 20px;
  }
}
